class MyComponent extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			error: null,
			isLoaded: false,
			items: []
		};
	}
	
	componentDidMount() {
		fetch("https://api.example.com/items")
			.then(res => res.json())
			.then(
			(result) => {
				this.setState({
					isLoaded: true,
					items: result.items
				});
			},
			// :     ,     catch(),    
			//     .
			(error) => {
				this.setState({
					isLoaded: true,
					error
				});
			}
			)
	}
	
	render() {
		const { error, isLoaded, items } = this.state;
		if (error) {
			return <div>: {error.message}</div>;
		} else if (!isLoaded) {
			return <div>...</div>;
		} else {
			return (
			<ul>
				{items.map(item => (
					<li key={item.id}>
					{item.name} {item.price}
					</li>
				))}
			</ul>
			);
		}
	}
}
